@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.content-body {
  background-image: url(./assets/images/bg-body.png);
  background-position: center;
}

.header {
  background-image: url(./assets/images/img-header.png);
  background-position: center;
  color: white;
  text-align: center;
  padding: 10px 0;
  font-size: 24px;
}

.header img {
  width: 151px;
  height: auto;
}

.container {
  max-width: 1200px;
  /* margin: 0 auto; */
  padding: 20px;
}

.game-section,
.similar-games {
  background-image: url(./assets/images/img-header.png);
  background-position: center;
  border-radius: 12px;
  color: white;
  margin-bottom: 16px;
  border-radius: 12px;
  display: flex;
}

.description {
  border-radius: 12px;
  color: white;
  padding: 32px;
  margin-bottom: 16px;
  display: block;
  background-color: #8db127;
}

.game-title {
  color: #ffffff;
  font-weight: 800;
}

.des-game {
  text-align: left;
}

.about-game {
  text-align: left;
  font-weight: 700;
}

.control {
  border-radius: 12px;
  color: white;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #333333;
}

.right-control {
  background-image: url(./assets/images/bg-control.png);
  background-position: center;
  display: block;
}

.game-screen {
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  border-radius: 8px;
}

.game-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
}

.game-item {
  background-color: white;
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.game-item img {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.3s ease;
}

.game-item:hover img {
  transform: scale(1.1);
}

.game-name {
  background-color: #101010;
  color: #ffffff;
  font-weight: 800;
  text-align: center;
  padding: 12px;
}

.load-more {
  text-align: center;
  margin-top: 26px;
}

.load-more button {
  background-color: #8db127;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 100px;
  margin-bottom: 40px;
}

.conten-footer {
  background-image: url(./assets/images/bg-footer.png);
  background-position: center;
}

.footer {
  color: white;
  text-align: center;
  padding: 19px 0;
}
